module.exports = "<div class=\"col-md-12 col-sm-12 col-xs-12\">\r\n    <form [formGroup]=\"changePasswordForm\" (ngSubmit)=\"onSubmit()\">\r\n        <div class=\"col-md-12 col-sm-12 col-xs-12 np\">\r\n            <div class=\"form-group label-floating\">\r\n                <label class=\"control-label\" for=\"old_password\">Current Password</label>\r\n                <input id=\"old_password\" type=\"password\" required class=\"form-control\" formControlName=\"old_password\">\r\n            </div>\r\n            <div *ngIf=\"changePasswordForm.controls.old_password.touched && !changePasswordForm.controls.old_password.valid\" class='alert alert-danger'>\r\n                <p *ngIf=\"changePasswordForm.controls.old_password.errors.required\">\r\n                    <span class=\"mat-icon\">\r\n                        <i class=\"material-icons\">report_problem</i>\r\n                    </span>\r\n                    Old Password is required.</p>\r\n                <p *ngIf=\"changePasswordForm.controls.old_password.errors.minlength\">\r\n                    <span class=\"mat-icon\">\r\n                        <i class=\"material-icons\">report_problem</i>\r\n                    </span>\r\n                    Use at least 8 characters\r\n                </p>\r\n            </div>\r\n            <div class=\"form-group label-floating\">\r\n                <label class=\"control-label\" for=\"new_password\">New Password</label>\r\n                <input id=\"new_password\" type=\"password\" required class=\"form-control\" formControlName=\"new_password\" (focus)=\"resetvalidation()\">\r\n            </div>\r\n            <div *ngIf=\"changePasswordForm.controls.new_password.touched && !changePasswordForm.controls.new_password.valid\" class='alert alert-danger'>\r\n                <p *ngIf=\"changePasswordForm.controls.new_password.errors.required\">\r\n                    <span class=\"mat-icon\">\r\n                        <i class=\"material-icons\">report_problem</i>\r\n                    </span>\r\n                    New Password is required.\r\n                </p>\r\n                <p *ngIf=\"changePasswordForm.controls.new_password.errors.minlength\">\r\n                    <span class=\"mat-icon\">\r\n                        <i class=\"material-icons\">report_problem</i>\r\n                    </span>\r\n                    Use at least 8 characters\r\n                </p>\r\n            </div>\r\n            <div class=\"form-group label-floating\">\r\n                <label class=\"control-label\" for=\"confirm_password\">Retype New Password</label>\r\n                <input id=\"confirm_password\" type=\"password\" required class=\"form-control\" formControlName=\"confirm_password\" (focus)=\"resetvalidation()\">\r\n            </div>\r\n            <div *ngIf=\"changePasswordForm.controls.confirm_password.touched && !changePasswordForm.controls.confirm_password.valid\" class='alert alert-danger'>\r\n                <p *ngIf=\"changePasswordForm.controls.confirm_password.errors.required\">\r\n                    <span class=\"mat-icon\">\r\n                        <i class=\"material-icons\">report_problem</i>\r\n                    </span>\r\n                    Confirm Password is required.\r\n                </p>\r\n                <p *ngIf=\"changePasswordForm.controls.confirm_password.errors.minlength\">\r\n                    <span class=\"mat-icon\">\r\n                        <i class=\"material-icons\">report_problem</i>\r\n                    </span>\r\n                    Use at least 8 characters\r\n                </p>\r\n            </div>\r\n            <div *ngIf=\"error\" class=\"alert alert-danger\">\r\n                <p>\r\n                    <span class=\"mat-icon\">\r\n                        <i class=\"material-icons\">report_problem</i>\r\n                    </span>{{Message}}\r\n                </p>\r\n            </div>\r\n            <div *ngIf=\"success\" class=\"val-success-msg form-group success-message hide\">\r\n                <span class=\"icon-success\">\r\n                    <i class=\"material-icons\">check_circle</i>\r\n                </span>\r\n                {{Message}}\r\n            </div>\r\n        </div>\r\n        <div class=\"col-md-12 col-sm-12 col-xs-12 np\">\r\n            <button [disabled]=\"!changePasswordForm.valid\" type=\"submit\" class=\"btn btn-white btn-hover\" id='updatePassword' (click)=\"callGA('UPDATEPASS')\">Update</button>\r\n        </div>\r\n    </form>\r\n</div>\r\n<!-- Start: changes updated successfully div -->\r\n<div class=\"float-changes-updated hide\">  \r\n    <div class=\"col-md-12 np\">\r\n        <span class=\"icon-done\"><i class=\"material-icons\">done</i></span>\r\n        <span>{{Message}}</span>\r\n    </div>\r\n</div>\r\n<!-- End: changes updated successfully div -->"

