         <!--</div>-->\r\n          <!--<div class=\"col-md-12 col-sm-12 col-xs-12 np billing-grey-bottom rs-hide\">-->\r\n          <!--<ul class=\"billing-list\">-->\r\n          <!--<li>-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">-->\r\n          <!--&nbsp;<span>1</span>&nbsp;User(s)-->\r\n          <!--</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--<li>-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">-->\r\n          <!--&nbsp;<span>10</span>&nbsp;Leads-->\r\n          <!--</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--<li>-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">-->\r\n          <!--&nbsp;<span>500</span>&nbsp;Visits-->\r\n          <!--</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--<li>-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">&nbsp;Embed</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--<li>-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">&nbsp;Lead Generation</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--<li>-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">&nbsp;Traffic Analytics</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--<li class=\"unselected\">-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">&nbsp;Integrations</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--<li class=\"unselected\">-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">&nbsp;Custom Branding</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--<li class=\"unselected\">-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">&nbsp;custom CTA</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--<li class=\"unselected\">-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">&nbsp;Priority Support</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--<li class=\"unselected\">-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">&nbsp;Account Manager</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--<li class=\"unselected\">-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">&nbsp;Api</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--<li class=\"unselected\">-->\r\n          <!--<a href=\"javascript:void(0);\">-->\r\n          <!--<span class=\"mat-icon\"></span>-->\r\n          <!--<span class=\"list-name\">&nbsp;Training</span>-->\r\n          <!--</a>-->\r\n          <!--</li>-->\r\n          <!--</ul>-->\r\n          <!--</div>-->\r\n          <!--<a href=\"javascript:void(0);\" class=\"toggle-dots-white\">-->\r\n          <!--<div>-->\r\n          <!--<i class=\"material-icons\">keyboard_arrow_down</i>-->\r\n          <!--<p class=\"more-info\">More Info</p>-->\r\n          <!--</div>-->\r\n          <!--</a>-->\r\n          <!--</div>-->\r\n\t\t\t\t\t<span *ngFor=\"let plan of viewplansList\">\r\n\t\t\t\t\t\t<span *ngIf=\"plan.id != 'starter'\">\r\n\t\t\t\t\t\t\t<div class=\"col-md-4 col-sm-4 col-xs-12 np\" [class.billing-grey] = \"plan.id != runningPlanDetail.id\" [class.starter-box] = \"plan.id == 'essentials_m' || plan.id == 'essentials_s' || plan.id == 'essentials_y'\" [class.billing-white]=\"runningPlanDetail.id == plan.id\" [class.business-box]=\"plan.id == 'business_m' || plan.id == 'business_s' || plan.id == 'business_y'\" [class.freelancer-box]=\"plan.id == 'freelancer_m' || plan.id == 'freelancer_s' || plan.id == 'freelancer_y'\" [class.business-highlight]=\"plan.id.split('_')[0] == 'business' && runningPlanDetail.id == 'appsumo_d'\" [class.enterprise-box]=\"plan.id=='enterprise_m'||plan.id=='enterprise_s'||plan.id=='enterprise_y'\" [class.selected-plan]=\"runningPlanDetail.id == plan.id\">\r\n\t\t\t\t\t\t\t\t<div class=\"col-md-12 col-sm-12 col-xs-12 np text-center\" [class.billing-white-top]=\"runningPlanDetail.id == plan.id\" [class.business-box-xshadow]=\"plan.id == 'business_m' || plan.id == 'business_s' || plan.id == 'business_y' || plan.id == 'essentials_m' || plan.id == 'essentials_s' || plan.id == 'essentials_y'\" [class.billing-grey-top]=\"runningPlanDetail.id != plan.id\">\r\n\t\t\t\t\t\t\t\t\t<span class=\"ribbon\" *ngIf=\"plan.id == 'business_m' || plan.id == 'business_s' || plan.id == 'business_y'\">Most Popular</span>\r\n\t\t\t\t\t\t\t\t\t<h3 class=\"plan-title\">{{plan.name.split(' ')[0]}}</h3>\r\n\t\t\t\t\t\t\t\t\t<h4 class=\"plan-title-cycle\">{{plan.name.split(' ')[1]}}</h4>\r\n\t\t\t\t\t\t\t\t\t<h3 class=\"plan-price plan-price-main\">\r\n\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id != 'starter'\">\r\n\t\t\t\t\t\t\t\t\t\t<span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngFor=\"let pL of planFeatures\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngFor=\"let cycle of pL.cycles\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"cycle.plan == plan.id.split('_')[0] && cycle.coupon_cycle == plan.id.split('_')[1]\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"!cycle.coupon_active\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span class=\"strike-off strike-price\" *ngIf=\"plan.id != 'freelancer_m' && (plan.id != 'essentials_m' || isAppsumoUser) &&plan.id != 'business_m' && plan.id.split('_')[0] !='enterprise' \">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{plan.currency_code}}\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[0] == 'essentials'\">{{essentials_m}}</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[0] == 'business'\">{{business_m}}</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[0] == 'freelancer'\">{{freelancer_m}}</span>\r\n\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t<span id=\"contact-text\" href=\"javascrpt:void(0);\" class=\"billing-contact\" *ngIf=\"plan.id.split('_')[0] == 'enterprise'\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t<!--<i class=\"material-icons\">headset_mic</i> -->\r\n\t\t\t\t\t\t\t\t\t\t\t\t<div class=\"billing-contact-text\">Need Enterprise grade Features? Contact Us</div>\r\n\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t<span class=\"actual-price\" *ngIf=\"plan.id.split('_')[1] == 'm' && (!isAppsumoUser || (plan.id != 'essentials_m' && isAppsumoUser)) && plan.id != 'enterprise_m'\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{plan.currency_code}} {{plan.price}} / Month\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t<span class=\"actual-price\" *ngIf=\"plan.id.split('_')[1] == 'm' && plan.id == 'essentials_m' && isAppsumoUser\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{plan.currency_code}} 45 / Month\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t<span class=\"actual-price\" *ngIf=\"plan.id.split('_')[1] == 's' && (!isAppsumoUser || (plan.id != 'essentials_s' && isAppsumoUser)) && plan.id != 'enterprise_s'\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{plan.currency_code}} {{toCeil(plan.price/6)}} / Month\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t<span class=\"actual-price\" *ngIf=\"plan.id.split('_')[1] == 's' && plan.id == 'essentials_s' && isAppsumoUser\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{plan.currency_code}} 40 / Month\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t<span class=\"actual-price\" *ngIf=\"plan.id.split('_')[1] == 'y' && (!isAppsumoUser || (plan.id != 'essentials_y' && isAppsumoUser)) && plan.id != 'enterprise_y'\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{plan.currency_code}} {{toCeil(plan.price/12)}} / Month\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t<span class=\"actual-price\" *ngIf=\"plan.id.split('_')[1] == 'y' && plan.id == 'essentials_y' && isAppsumoUser\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{plan.currency_code}} 35 / Month\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"cycle.coupon_active\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span class=\"strike-off strike-price\" *ngIf=\"plan.id.split('_')[1] != 'm' && !isAppsumoUser\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{plan.currency_code}}\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[0] == 'essentials'\">{{essentials_m}}</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[0] == 'business'\">{{business_m}}</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[0] == 'freelancer'\">{{freelancer_m}}</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t<span class=\"actual-price\" *ngIf=\"cycle.coupon_active && plan.id.split('_')[0] != 'enterprise'\">\r\n                                {{plan.currency_code}}\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"cycle.coupon_type=='PERCENTAGE'\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[1] == 'y' && !isAppsumoUser\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{toCeil(plan.price/12)-(toCeil(plan.price/12)*cycle.coupon_value/100)}}\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[1] == 'y' && plan.id.split('_')[0] == 'essentials' && isAppsumoUser\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t35\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[1] == 'm' && plan.period == 6 && (!isAppsumoUser || (plan.id != 'essentials_s' && isAppsumoUser))\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{toCeil(plan.price/6)-(toCeil(plan.price/6)*cycle.coupon_value/100)}}\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[1] == 'm' && plan.period == 6 && plan.id.split('_')[0] == 'essentials' && isAppsumoUser\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t40\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[1] == 'm' && plan.period == 1 && (!isAppsumoUser || (plan.id != 'essentials_m' && isAppsumoUser))\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{plan.price-(plan.price*cycle.coupon_value/100)}}\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[1] == 'm' && plan.period == 1 && plan.id.split('_')[0] == 'essentials' && isAppsumoUser\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t45\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"cycle.coupon_type!='PERCENTAGE'\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[1] == 'y' && (!isAppsumoUser || (plan.id != 'essentials_y' && isAppsumoUser))\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{toCeil(plan.price/12)-cycle.coupon_value}}\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[1] == 'y' && plan.id.split('_')[0] == 'essentials' && isAppsumoUser\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t35\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[1] == 'm' && plan.period == 6 && (!isAppsumoUser || (plan.id != 'essentials_s' && isAppsumoUser))\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{toCeil(plan.price/6)-cycle.coupon_value}}\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[1] == 'm' && plan.period == 6 && plan.id.split('_')[0] == 'essentials' && isAppsumoUser\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t40\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[1] == 'm' && plan.period == 1 && (!isAppsumoUser || (plan.id != 'essentials_m' && isAppsumoUser))\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t{{plan.price-cycle.coupon_value}}\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id.split('_')[1] == 'm' && plan.period == 1 && plan.id.split('_')[0] == 'essentials' && isAppsumoUser\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t45\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t/ Month\r\n\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t<small *ngIf=\"cycle.coupon_active\" class=\"apply-coupon\">Use Coupon code : {{cycle.coupon_name}}</small>\r\n\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t\t<span *ngIf=\"plan.id == 'starter'\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t{{plan.price}}\r\n\t\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t\t\t<span class=\"appsumoPricing\" *ngIf=\"isAppsumoUser && plan.id.split('_')[0] == 'essentials'\"><img src=\"assets/images/appSumoPricing.png\"></span>\r\n\t\t\t\t\t\t\t\t\t</h3>\r\n\t\t\t\t\t\t\t\t\t<!--<p>{{plan.description}}</p>-->\r\n\t\t\t\t\t\t\t\t\t<div class=\"col-md-12 col-sm-12 col-xs-12 np text-center\" *ngIf=\"runningPlanDetail.id != plan.id && plan.id.split('_')[0] != 'enterprise' && userRole == 'ADMIN'\">\r\n\t\t\t\t\t\t\t\t\t\t<span>\r\n\t\t\t\t\t\t\t\t\t\t\t<button type=\"\" class=\"btn btn-white-red-outline btn-upgrade  btn-hover\" *ngIf=\"runningPlan == plan.id.split('_')[0]\" (click)=\"showEstimateModal(plan, false);callGA('CHANGECYCLE')\">Change Cycle</button>\r\n\r\n\t\t\t\t\t\t\t\t\t\t\t<button type=\"\" class=\"btn btn-white-red-outline btn-upgrade  btn-hover\" id=\"popupOpen\" *ngIf=\"(plan.id != 'starter' && plan.id != 'freemium') && runningPlan != plan.id.split('_')[0]\" (click)=\"showEstimateModal(plan,false);callGA('CHANGEPLAN');contactUs()\">Change Plan</button>\r\n\r\n\t\t\t\t\t\t\t\t\t\t\t<button type=\"\" class=\"btn btn-white-red-outline btn-upgrade  btn-hover\" *ngIf=\"!futurePlanSubscription && (plan.id == 'starter' || plan.id == 'freemium')\" (click)=\"showEstimateModal(plan,false);callGA('CHANGEPLAN')\">Change Plan</button>\r\n\t\t\t\t\t\t\t\t\t\t</span>\r\n\r\n                    <!--<span *ngIf=\"plan.id.split('_')[0] == 'enterprise'\">-->\r\n                    <!--<button type=\"\" class=\"btn btn-white-red-outline btn-upgrade btn-hover intercom_trigger\" (click)=\"contactUs()\">Contact Us</button>-->\r\n                    <!--</span>-->\r\n\r\n\t\t\t\t\t\t\t\t\t\t<span type=\"\" *ngIf=\"futurePlanSubscription && plan.id == 'starter'\" class=\"plan-act\">\r\n\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\t\tPlan activates on <span *ngIf=\"runningPlanSubscription.status == 'in_trial'\">{{runningPlanSubscription.trial_end}}.</span>\r\n\t\t\t\t\t\t\t\t\t\t<span\r\n                      *ngIf=\"runningPlanSubscription.status == 'active'\">{{runningPlanSubscription.current_term_end}}.</span>\r\n\t\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t</div>\r\n\t\t\t\t\t\t\t\t\t<div class=\"CONTACT US col-md-12 col-sm-12 col-xs-12 np text-center\" *ngIf=\"runningPlanDetail.id != plan.id && plan.id.split('_')[0] == 'enterprise' && userRole == 'ADMIN'\">\r\n\t\t\t\t\t\t\t\t\t\t<span>\r\n\t\t\t\t\t\t\t\t\t\t\t<button type=\"\" class=\"btn btn-white-red-outline btn-upgrade  btn-hover\" *ngIf=\"runningPlan == plan.id.split('_')[0]\" (click)=\"showEstimateModal(plan, false);callGA('CHANGECYCLE')\">Change Cycle</button>\r\n\r\n\t\t\t\t\t\t\t\t\t\t\t<a href=\"javascript:void(0);\" class=\"intercom_trigger btn btn-light-grey-outline btn-light-grey-hover btn-contact\" id=\"popupOpen\" *ngIf=\"(plan.id != 'starter' && plan.id != 'freemium') && runningPlan != plan.id.split('_')[0]\" (click)=\"contactUs($event)\">CONTACT US</a>\r\n\r\n\t\t\t\t\t\t\t\t\t\t\t<a href=\"javascript:void(0);\" class=\"intercom_trigger btn btn-light-grey-outline btn-light-grey-hover btn-contact\" *ngIf=\"!futurePlanSubscription && (plan.id == 'starter' || plan.id == 'freemium')\" (click)=\"contactUs($event)\">CONTACT US</a>\r\n\t\t\t\t\t\t\t\t\t\t</span>\r\n\t\t\t\t\t\t\t\t\t</div>\r\n\t\t\t\t\t\t\t\t\t<div class=\"col-md-12 col-sm-12 col-xs-12 np text-center\" *ngIf=\"runningPlanDetail.id == plan.id  && userRole == 'ADMIN'\">\r\n\t\t\t\t\t\t\t\t\t\t<button type=\"\" class=\"btn btn-light-grey-outline btn-light-grey-hover\" *ngIf=\"!promocodes && payment_status != 'no_card' && runningPlanSubscription.status != 'cancelled'\">\r\n                      Your Current Plan\r\n                    </button>\r\n\t\t\t\t\t\t\t\t\t\t<button type=\"\" class=\"btn btn-white-red-outline btn-hover reactivate\" *ngIf=\"runningPlanSubscription.status == 'cancelled'\"\r\n                            (click)=\"showEstimateModal(plan, true)\">Reactivate</button>\r\n                    <!--<span *ngIf=\"promocodes && runningPlanSubscription.status != 'cancelled'\" class=\"promoapplied\">Promocode applied: {{promocodes}}</span>-->\r\n                    <button class=\"btn btn-light-grey-outline btn-light-grey-hover\" *ngIf=\"promocodes && runningPlanSubscription.status != 'cancelled' && plan.id.split('_')[0] != 'enterprise'\"\r\n                            (click)=\"callGA('PAYMENTCLICK');changeToRegularPlanA(plan)\">CHANGE PLAN</button>\r\n\t\t\t\